.age-modal {
	background-image: url('../../../../uploads/2019/10/wbw-diamond.svg');
	background-repeat: no-repeat;
	background-position: 50% 50%;
	min-height: 300px;
}
.wbw-home-head {
	min-height: 700px;
	width: 100%;
	background-image: url('../../../../uploads/2019/10/wbw-beginnings.jpg');
	background-position: 50% 50%;
	background-size: cover;
	border-bottom: 4px solid #b6a268;
	@include breakpoint (md) {
		min-height: 1000px;
	}
}
.wbw-footer {
	min-height: 100px;
	background: #13426b;
	border-top: 4px solid #b6a268;
}
.logo-img {
	padding-top: 170px;
	filter:drop-shadow(1px 1px 1px #fff);
}
.event-carousel-area {
	width: 100%;
	min-height: 200px;
	background: #ccc;
}
.mission {
	background: #13426b;
	width: 100%;
	min-height: 300px;
	border-bottom: 4px solid #b6a268;
}
.kill-padding {
	 @extend %kill-padding;
}
.wbw-social {
	width: 100%;
	min-height: 250px;
	background: #b6a268;
	border-top: 4px solid #13426b;
}
.wbw-card {
	min-height: 200px;
}
		
